<template>
  <div class="container">
    <div class="row">
      <div class="col s12 m8 offset-m2">
        <div class="login card-panel grey lighten-4 green-text center">
          <h3>Welcome back!</h3>
          <form action="index.html">
            <div class="input-field">
              <i class="material-icons prefix">email</i>
              <input type="email" id="email" v-model="email">
              <label for="email">Email Address</label>
            </div>
            <div class="input-field">
              <i class="material-icons prefix">lock</i>
              <input type="password" id="password" v-model="password">
              <label for="password">Password</label>
            </div>
            <button v-on:click="login" class="btn btn-large btn-extended green lighten-1 white-text">Login</button>
          </form>
        </div>
        <div class="col s12 m8 offset-m2">
          <h6>Please contact administrator for login issues</h6>       
        </div> 
      </div>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase'
export default {
  name: 'login',
  data () {
    return {
      email: '',
      password: ''
    }
  },
  methods: {
    login: function (e) {
      firebase
      .auth()
      .signInWithEmailAndPassword(this.email, this.password)
      .then(
        user => {
          alert('Login success, welcome back!')
          this.$router.go({path: this.$router.path})
      },
      err => {
        alert(err.message)
      })
      e.preventDefault()
    }
  }
}
</script>




